<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="销售组织" size="mini">
          <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="getcompanyFocus" @change="companyChange" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="省" size="mini">
          <el-select clearable v-model="params.province_id" @focus="getSF" placeholder="请选择" filterable @change="clear(1)">
            <el-option :label="item.area_name" :value="item.area_id" :key="item.area_id" v-for="item in province.options"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="市" size="mini">
          <el-select clearable v-model="params.city_id" @focus="getSQ" placeholder="请选择" filterable @change="clear(2)">
            <el-option :label="item.area_name" :value="item.area_id" :key="item.area_id" v-for="item in city.options"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="区/县" size="mini">
          <el-select clearable v-model="params.area_id" @focus="getQX" placeholder="请选择" filterable>
            <el-option :label="item.area_name" :value="item.area_id" :key="item.area_id" v-for="item in area.options"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="选择客户" size="mini">
          <el-select v-model="params.customer_id" filterable placeholder="请选择" clearable @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="原业务员" size="mini">
          {{(function(){if(params.employee_id == 0)params.employee_id = null})()}}
          <el-select v-model="params.employee_id" @focus="getbusinIdFoucs" filterable clearable>
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList.options"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" size="mini">
          <el-checkbox style="margin-left:70px;" false-label="0" true-label="1" v-model="params.bind_type" name="type">显示已绑定业务员的客户</el-checkbox>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary" style="margin-left:50px;">查询</el-button>
          <!-- <el-button plain @click="onReset"  size="mini">重置</el-button> -->
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container" style="width:100%;height:300px;">
      <div style="width: 600px;float:left">
        <span style="padding-bottom:10px;">待绑定客户：</span>
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="leftList" height="242" style="width: 600px;float:left;margin-top:5px;cursor:pointer" @cell-dblclick="handleSelectionChange">
          <el-table-column :show-overflow-tooltip="true" property="customer_name" width="100" label="客户名称"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="contact_name" width="100" label="联系人"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_mobile" width="100" label="联系电话"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" width="200" label="区域">
            <template slot-scope="scoped">
              {{scoped.row.province_name}}/{{scoped.row.city_name}}/{{scoped.row.area_name}}
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="employee_name" width="100" label="原业务员"></el-table-column>
        </el-table>
      </div>
      <div style="float:left;margin-top:100px;width:50px;">
        <el-button @click="ontabRight" icon="el-icon-d-arrow-right" size="mini" style="display:block;margin:0 0 10px 10px;"></el-button>
        <el-button @click="ontabLeft" icon="el-icon-d-arrow-left" size="mini" style="display:block;"></el-button>
      </div>
      <div style="width: 600px;float:right">
        <span style="padding-bottom:10px;">绑定客户：</span>
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="rightList" height="242" style="width: 600px;float:right;margin-top:5px;cursor:pointer" @cell-dblclick="handleSelectionChange2">
          <el-table-column :show-overflow-tooltip="true" property="customer_name" width="100" label="客户名称"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="contact_name" width="100" label="联系人"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_mobile" width="100" label="联系电话"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" width="200" label="区域">
            <template slot-scope="scoped">
              {{scoped.row.province_name}}/{{scoped.row.city_name}}/{{scoped.row.area_name}}
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="employee_name" width="100" label="原业务员"></el-table-column>
        </el-table>
      </div>
    </div>

    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="待绑定业务员" size="mini" prop="business_id" :rules="rules.business_id">
          <el-select v-model="params.business_id" @focus="getbusinIdFoucs" filterable>
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList.options"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogCancelForm">取 消</el-button>
      <el-button type="primary" @click="dialogSubmitForm()">确 定</el-button>

    </div>
  </div>
</template>

<script>
import {
  getData,
  getCompanyListCommon,
  getCustomerList,
  getDepartmentListCommon,
  bindIdsF
} from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      radio: null,
      order_id: null,
      timeValue: [],
      leftList: [],
      rightList: [],
      params: {},
      loading: false,
      searchParams: {},
      TableData: {
        tableData: [],
        total: 10
      },
      province: {
        options: []
      }, //省
      city: {
        options: []
      }, //市
      area: {
        options: []
      }, //区
      // customerList: {
      //   options: []
      // },
      employeeList: {
        options: []
      },
      departmentList: {
        options: []
      },
      companyList: [],
      per_page: [30, 50, 100, 500],
      pageSize: 30,
      goPage: 1,
      is_shop: 0
    };
  },
  // props: {
  //   warehouseId: {
  //     type: [Number, String]
  //   }
  // },
  props: ["companyidspop"],
  methods: {
    getbusinIdFoucs() {
      this.$common.options("getEmployeeListOption", this.employeeList, {
        company_id: this.params.company_id
      }); //原业务员
    },
    clear(type) {
      if (type == 1) {
        this.$set(this.params, "city_id", null);
        this.$set(this.params, "area_id", null);
      } else if (type == 2) {
        this.$set(this.params, "area_id", null);
      }
    },
    clearBusiss() {
      this.$set(this.params, "business_id", null);
    },
    ontabLeft() {
      this.$set(this, "leftList", []);
      this.$set(this, "rightList", []);
      this.$axMethod.getBindingList(this.params).then(response => {
        if (response.data.code == 200) {
          this.$set(this, "leftList", response.data.data);
        }
      });
    },
    ontabRight() {
      let a = this.rightList;
      a.push.apply(a, this.leftList);
      this.$set(this, "rightList", a);
      this.$set(this, "leftList", []);
    },
    getDepartment() {
      getDepartmentListCommon(this, this.params); //部门
    },
    getcompanyFocus() {
      getCompanyListCommon(this); //获取组织
    },
    handleSelectionChange(row, column, cell, event) {
      let a = [];
      this.leftList = this.leftList.filter(obj => {
        return obj.index != row.index;
      });
      this.rightList.push(row);
    },
    handleSelectionChange2(row) {
      let a = [];
      this.rightList = this.rightList.filter(obj => {
        return obj.index != row.index;
      });
      this.leftList.push(row);
    },
    getSF() {
      let params1 = {
        level: 2 // level: 2省 3市 4县
      };
      let _this = this;
      _this.$axMethod.getAreaList(params1).then(res => {
        _this.province.options = res.data.data;
      });
    },
    getSQ() {
      let params1 = {
        level: 3,
        p_id: this.params.province_id
      };
      let _this = this;
      _this.$axMethod.getAreaList(params1).then(res => {
        _this.city.options = res.data.data;
      });
    },
    getQX() {
      let params1 = {
        level: 4,
        p_id: this.params.city_id
      };
      let _this = this;
      _this.$axMethod.getAreaList(params1).then(res => {
        _this.area.options = res.data.data;
      });
    },
    getbusinIdFoucs() {
      this.$common.options("getEmployeeListOption", this.employeeList, {
        company_id: this.params.company_id,
        department_id: this.params.department_id
      }); //业务员
    },
    dialogCancelForm() {
      this.$emit("closedialog", false);
    },
    dialogSubmitForm() {
      if (this.rightList.length <= 0) {
        this.$message.error("请添加绑定客户");
      } else if (
        this.params.business_id == null ||
        this.params.business_id == undefined ||
        this.params.business_id == ""
      ) {
        this.$message.error("请添加待绑定业务员");
      } else {
        var addr = [];
        var addrnew = [];
        for (var i = 0; i < this.rightList.length; i++) {
          addr.push(this.rightList[i].customer_id);
          addrnew.push(this.rightList[i].associated_customer_id);
        }
        let rebateArr = addr.toString();
        let rebateArrNew = addrnew.toString();
        let a = {
          business_id: this.params.business_id,
          customer_id: rebateArr,
          associated_customer_id: rebateArrNew
        };

        this.$axMethod.bindingSalesman(a).then(response => {
          if (response.data.code == 200) {
            this.$message({
              message: response.data.data,
              type: "success"
            });

            this.dialogCancelForm();
            this.$emit("dialogformInline", false);
          }
        });
      }
    },
    // getCustomerListRemote(query) {
    //   getCustomerList(this, {
    //     company_id: this.params.company_id,
    //     customer_name: query
    //   });
    // },
    onSearch() {
      this.$set(this, "leftList", []);
      this.$set(this, "rightList", []);
      this.$axMethod.getBindingList(this.params).then(response => {
        if (response.data.code == 200) {
          this.$set(this, "leftList", response.data.data);
        }
      });
    },
    companyChange(val) {
      if (val == 1) {
        getDepartmentListCommon(this, this.params);
      } else {
        getDepartmentListCommon(this, this.params, 1);
      }
    }
  },
  mounted() {
    let { employee_id, company_admin_id } = localStorage;
    //0 1 2 后台返回的1和2 对应的是超管

    if (this.companyidspop.length > 0) {
      this.companyidspop.forEach((item, index) => {
        item.index = index;
      });
      this.$set(this, "leftList", this.companyidspop);
      if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
        this.$set(this, "params", {
          company_id: this.companyidspop[0].company_id
            ? this.companyidspop[0].company_id
            : Number(localStorage.company_id),
          bind_type: 0
        });
      } else {
        this.$set(this, "params", {
          company_id: this.companyidspop[0].company_id
            ? this.companyidspop[0].company_id
            : Number(localStorage.company_id),
          bind_type: 0
        });
      }
      bindIdsF(this, this.companyidspop[0], [
        ["company_id", "company_name", "companyList"]
      ]);
    } else {
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
        business_id: "",
        bind_type: 0
      });
      bindIdsF(this, localStorage, [
        ["company_id", "company_name", "companyList"]
      ]);
    }
    bindIdsF(this, localStorage, [
      ["employee_id", "employee_name", "employeeList", "options"]
    ]);
  }
};
</script>


<style lang="less" scoped>
.el-table__row td {
  cursor: pointer;
}
</style>
