<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="销售组织" size="mini">
          <el-select clearable v-model="params.company_id" placeholder="请选择" @change="clearBusiss" @focus="companyListInvok" filterable maxlength="100">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="创建日期" size="mini">
          <!-- suffix-icon="" -->
          <el-date-picker v-model="timeValue" type="daterange" @change="changeDate" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            <i slot="suffix" class="el-input__icon el-icon-date"></i>
          </el-date-picker>
        </el-form-item>

        <el-form-item label="区域" size="mini" prop="cityList" class="wauto">
          <getAreaList v-if="defaultArea && defaultArea.length" @changeDefaultArea="changeDefaultArea" @focus="getprovinceFocus" :defaultArea="defaultArea" />
        </el-form-item>
        <el-form-item label="客户名称" size="mini">
          <!-- <el-input v-model="params.customer_name" maxlength="100" placeholder="请输入"></el-input> -->
          <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" @change="getCustomerChange" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户分类" size="mini">
          <el-select clearable v-model="params.customer_type_id" placeholder="请选择" @focus="customerTypeInvok" @change="customer_type_change" filterable remote>
            <el-option :label="item.customer_type_name" :value="item.customer_type_id" :key="item.customer_type_id" v-for="item in customerTypeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务员" size="mini">
          {{(function(){if(params.employee_id == 0)params.employee_id = null})()}}
          <el-select v-model="params.employee_id" @focus="EmployeeListInvok" filterable @change="businessChange" clearable>
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="联系人" size="mini">
          <el-input v-model="params.contact_name" maxlength="100" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="联系电话" size="mini">
          <el-input v-model="params.customer_mobile" maxlength="100" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item size="mini">
          <el-checkbox style="margin-left:120px;" false-label="0" true-label="1" v-model="params.is_disabled" name="type">已停用</el-checkbox>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addFun" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
      <el-button plain @click="bindBusiness" size="mini" :disabled="btnShow" v-if="authority.b_bind==1">绑定业务员</el-button>
      <el-button plain @click="unbind" size="mini" v-if="authority.b_unbind==1">解绑业务员</el-button>
      <el-button plain icon="el-icon-download" size="mini" @click="downLoadFile">下载模板</el-button>
      <el-button plain icon="iconfont icon-daoru5" @click="dialogEmployee" class="addBtn" v-if="authority.b_import==1" size="mini">导入</el-button>

      <el-dropdown style="margin-left:10px;" v-if="authority.b_export==1">
        <span split-button style="cursor:pointer;color:#4e90e2">
          <el-button plain icon="iconfont icon-daochu10" size="mini">{{fileter}}</el-button>
        </span>
        <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
          <el-dropdown-item @click.native="exportFile(1)">导出Excel文件</el-dropdown-item>
          <el-dropdown-item @click.native="exportFile(2)">导出CSV逗号分隔</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" :row-class-name="tableRowClassName" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" fixed="left" align="center" width="44"></el-table-column>
        <el-table-column label="操作" fixed="right" width="100">
          <template slot-scope="scope">
            <el-button @click="examine(scope.row)" type="text" size="small" v-if="authority.b_info==1">
              查看
            </el-button>
            <el-button @click="handleEdit(scope.row)" type="text" size="small" v-if="authority.b_edit==1">
              修改
            </el-button>
            <el-button disabled type="text" size="small" v-else>
              修改
            </el-button>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" width="200" label="销售组织"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_id" width="100" label="客户编号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_name" width="120" label="客户名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="logogram" width="100" label="助记码"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_type_name" width="150" label="客户分类"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="overdue_period" width="150" label="账龄期限"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="province_name" width="200" label="所属区域">
          <template slot-scope="scope">
            {{scope.row.province_name}} {{scope.row.city_name}} {{scope.row.area_name}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="address" width="220" label="详细地址"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="contact_name" width="220" label="联系人"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_mobile" width="130" label="联系电话(手机)"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="employee_name" width="200" label="业务员"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="is_disabled_name" width="100" label="是否停用"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_user_name" width="150" label="创建人"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_time" width="200" label="创建时间"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="update_user_name" width="200" label="修改人"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="update_time" width="200" label="修改时间"></el-table-column>
        <!--<el-table-column  :show-overflow-tooltip="true" property="remark" width="200" label="备注"></el-table-column>-->
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
    <upload :options="options" @receiveUploadMsg="receiveUploadMsg" ref="upload"></upload>
    <!-- 绑定业务员弹框 -->
    <div v-if="dialogBillVisible">
      <el-dialog :close-on-click-modal="false" title="添加绑定关系" class="dialogForm" :visible.sync="dialogBillVisible" width="1300px" @closedialog="closedialog">
        <source-bill @dialogformInline="dialogformInline" :companyidspop="companyidspop" @closedialog="closedialog"></source-bill>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  getData,
  opthionList,
  exportFile //导出
} from "common/utils/public_function/commonUtils";
import getAreaList from "common/components/public/getAreaList";
import sourceBill from "./sourceBill.vue";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "customerinfoDefault",
  data() {
    return {
      authority: {},
      options: {
        title: "客户信息导入",
        url: "/v2/custom/Customer/importCustomer"
      },
      companyidspop: "", //默认传递的id
      btnShow: false,
      dialogBillVisible: false,
      fileter: "全部导出",
      loading: false,
      defaultArea: [0], //默认的区域id
      timeValue: [],
      province: {
        options: []
      },
      city: {
        options: []
      },
      area: {
        options: []
      },
      multipleSelection: []
    };
  },
  components: {
    getAreaList, //区域列表
    sourceBill //添加绑定关系
  },
  computed: {
    ...mapGetters(["customerinfoDefault_showGet"])
  },
  methods: {
    dialogEmployee() {
      this.$refs.upload.stateOpen();
    },
    tableRowClassName({ row, rowIndex }) {
      if (row.canNotDelete) {
        return "error-row";
      }
      return "";
    },
    clearBusiss(val) {
      this.$set(this.params, "employee_id", ""); // wz alter 清空业务员选中项
      this.$set(this.searchParams, "business_id", null);
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
    },
    receiveUploadMsg(response) {
      getData(this.searchParams);
    },
    getCustomerChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    customer_type_change(val) {
      if (val) {
        this.params.customer_type_name = this.customerTypeList.filter(
          obj => obj.customer_type_id === val
        )[0].customer_type_name;
      }
    },
    businessChange(val) {
      if (val) {
        this.params.employee_name = this.employeeList.filter(
          obj => obj.employee_id === val
        )[0].employee_name;
      }
    },
    dialogformInline(val) {
      getData(this.searchParams); // 成功之后更新列表
    },
    closedialog() {
      this.dialogBillVisible = false;
    },
    bindBusiness() {
      // 绑定业务员
      this.dialogBillVisible = true;
    },
    unbind() {
      //解绑
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要解绑的客户",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].associated_customer_id);
      }
      var creditArr = addr.toString();
      var funcan = {
        examineID: "associated_customer_id",
        eachData: {
          associated_customer_id: creditArr
        },
        examineApi: "UntieTheSalesman",
        list: {
          UrlApi: "getCustomerList",
          data: this.TableData,
          esData: this.params
        }
      };
      this.$common.examineFun(funcan);
    },
    getprovinceFocus() {
      this.$common.options("getAreaListOption", this.province, {
        level: 2
      });
    },
    exportFile(index) {
      //导出
      if (this.listShow == false) {
        this.$message({
          message: "请查询需要导出的数据",
          type: "error"
        });
        return false;
      }

      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].customer_id);
      }
      var rebateArr = addr.toString();
      let arg = {
        params: {
          ...this.params,
          customer_id: rebateArr,
          export_type: index
        },
        API: "exportCustomerUrl"
      };
      exportFile(arg);
    },
    downLoadFile() {
      //  下载模板
      this.$axMethod.cusdowntemplet().then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    changeDate(val) {
      this.params.start_time = val[0];
      this.params.end_time = val[1];
    },
    changeProvince() {
      this.params.city_id = "";
      this.$common.options("getAreaListOption", this.city, {
        level: 3,
        p_id: this.params.province_id
      });
    },
    changeCity() {
      this.$common.options("getAreaListOption", this.area, {
        level: 4,
        p_id: this.params.city_id
      });
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: this.goPage || 1
      });
      this.customerinfoIndexACT({
        // commont: "customerinfoAdd",
        params: this.params,
        goPage: this.goPage || 1,
        pageSize: this.pageSize
      });
      getData(this.searchParams);
    },
    onReset() {
      this.defaultArea = [0];
      this.$set(this, "params", {
        is_disabled: ""
      });
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getCustomerList", //接口名
        vm: this //传递的this
      });
      this.onSearch();
    },
    ...mapActions([
      "customerinfoAdd_show",
      "customerinfoSearch_show",
      "customerinfoIndexACT"
    ]),
    addFun() {
      this.customerinfoAdd_show({
        // commont: "customerinfoAdd",
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/custom/customerinfo/customerinfo_add"
      });
    },
    deletes() {
      var deledata = {
        deleID: "associated_customer_id",
        deleSelect: this.multipleSelection,
        deleApi: "delCustomer",
        list: {
          UrlApi: "getCustomerList",
          data: this.TableData
        }
      };
      this.$common.BatchDeletion(deledata, "删除", this);
    },
    examine(row) {
      this.customerinfoSearch_show({
        // commont: "customerinfoSearch",
        customer_id: row.customer_id,
        associated_customer_id: row.associated_customer_id,
        goPage: this.goPage,
        pageSize: this.pageSize,
        params: this.params
      });
      this.$router.push({
        path: "/custom/customerinfo/customerinfo_search"
      });
    },
    handleEdit(row) {
      this.customerinfoAdd_show({
        // commont: "customerinfoAdd",
        customer_id: row.customer_id,
        associated_customer_id: row.associated_customer_id,
        goPage: this.goPage,
        pageSize: this.pageSize,
        params: this.params
      });
      this.$router.push({
        path: "/custom/customerinfo/customerinfo_add"
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      this.btnShow = !this.multipleSelection.every(
        item => item.company_name === this.multipleSelection[0].company_name
      );
      this.companyidspop = this.multipleSelection; // 需要增加公司id
      if (this.multipleSelection.length) {
        this.fileter = "导出";
      } else {
        this.fileter = "全部导出";
      }
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    if (
      this.customerinfoDefault_showGet &&
      this.customerinfoDefault_showGet.params
    ) {
      let objMessage = this.customerinfoDefault_showGet.params;
      this.defaultArea = [
        objMessage.province_id,
        objMessage.city_id,
        objMessage.area_id
      ];
      if (this.customerinfoDefault_showGet.params.start_time) {
        const {
          start_time,
          end_time
        } = this.customerinfoDefault_showGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      this.$set(this, "params", this.customerinfoDefault_showGet.params);
      opthionList(this, this.customerinfoDefault_showGet.params);
    }
    if (
      this.customerinfoDefault_showGet &&
      this.customerinfoDefault_showGet.goPage
    ) {
      this.params.page = this.customerinfoDefault_showGet.goPage;
      this.params.per_page = this.customerinfoDefault_showGet.pageSize;
    }
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getCustomerList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
  }
};
</script>

<style scoped>
.customer-ul4 {
  width: 600px;
}

.customer-ul4 li:nth-child(1) {
  width: 90px;
}

.customer-ul4 li:nth-child(2) {
  width: 130px;
}

.customer-ul4 li:nth-child(3) {
  width: 120px;
  text-align: center;
}

.customer-ul4 li:nth-child(4) {
  width: 120px;
  text-align: left;
}
.customer-ul4 li:nth-child(5) {
  width: 140px;
  text-align: right;
}
</style>
